<template>
  <div class="page404">
    <el-container>
      <el-main>
        <el-button type="primary" icon="el-icon-menu" @click="$router.push('/')">
          回到首页
        </el-button>
        <el-button type="primary" icon="el-icon-back" @click="$router.go(-1)">
          返回上一页
        </el-button>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'page404',
    data() {
      return {};
    }
  };

</script>

<style scoped>
  .el-main {
    width: 600px;
    height: 350px;
    margin: 0 auto;
    position: fixed;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    text-align: center;
    background: url('../../assets/404.png') no-repeat center top;
  }

  .el-button {
    margin-top: 185px
  }

</style>



